import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import App from './App';
import Task from './components/reactstrap/task';
import List from './components/reactstrap/list';
import Goal from './components/goal';
import Day from './components/day';
import task_new from "./components/task_new";
import DaysList from './components/days';
import GoalUpdate from './components/goal_update';
import MyCalendar from './components/calendar';
import GoalTabs from './components/goal_tabs';
import Subgoal from './components/subgoal';


class Routes extends React.Component {

    render () {
        return (
            <Router>
                <App>
                    <Route exact path="/" component={List}/>
                    <Route exact path="/goal/goal" component={List}/>
                    <Route path="/goal/goal/:id" component={Goal} />
                    <Route path="/goal/subgoal/:id" component={Subgoal} />
                    <Route path="/goal/update/:id" component={GoalUpdate} />
                    <Route path="/goal/create_task" component={task_new} />
                    <Route path="/days" component={DaysList} />
                    <Route path="/day/:id" component={Day} />
                    <Route path="/calendar" component={MyCalendar} />
                    <Route path="/goal-tabs" component={GoalTabs} />
                    <Route path="/task/:id" component={Task} />
                </App>
            </Router>
        );
    }
}



export default Routes;