import React from 'react';
import {
    Field,
    reduxForm
} from 'redux-form';
import { renderField, renderDatePicker, renderTimePicker, renderTextarea } from './form-component';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import moment from 'moment';
import axios from 'axios';
import Grid from '@material-ui/core/Grid';
import { href } from '../utils';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import Button from '@material-ui/core/Button';



class GoalNew extends React.Component {

    state = {
        open: false
    }

    handleOpen = () => {
         this.setState({
             open: true
         });
    };

    handleClose = () => {
         this.setState({
             open: false
         });
    };

    onSubmit = (values) => {
        var date = moment(values.date).format("YYYY-MM-DD");
        var time = moment(values.time).format("HH:mm:ss");
        var newValues = {...values, date, time};
        if(this.props.goal_id){
            newValues = {...newValues, parent: this.props.goal_id}
        }
        axios({
            method: 'post',
            url: `${href}api/goal/`,
            data: newValues,
            headers: {
                Authorization: `Token ${localStorage.getItem('token')}`
            }    
        }).then(() => {
            if(this.props.refresh){
                this.props.refresh()
            }
            this.handleClose();
        });
    }

    render(){
        const { handleSubmit } = this.props;
        return (
            <MuiThemeProvider>
            <div onClick={this.handleOpen}
              style={{float: "left"}} 
            >{this.props.children}</div>
            <Dialog 
              onClose={this.handleClose} 
              aria-labelledby="add new task" 
              open={this.state.open}
              style={{direction:"rtl", textAlign: "right"}}
              scroll="body"
            >
                <DialogTitle>إضافة هدف جديد</DialogTitle>
                <form onSubmit={handleSubmit(this.onSubmit)} style={{padding:12}}>
                    <Grid container spacing={24}>

                    <Grid item xs={12}>
                    <Field name="title" component={renderField} type="text" label="الهدف" validate={required} />
                    </Grid>

                    <Grid item xs={12}>
                        <Field name="description" component={renderTextarea} label="التفاصيل" />
                    </Grid>

                    <Grid item sm={6}>
                        <Field name="total_time" component={renderField} type="number" label="الوقت الكلى بالساعات" validate={required} />
                    </Grid>
                    <Grid item sm={6}>
                        <Field name="total_spend" component={renderField} type="number" label="الوقت المنقضى بالساعات" validate={required} />
                    </Grid>

                    <Grid item sm={6}>
                        <Field name="date" component={renderDatePicker} label="تاريخ الانتهاء"/>
                    </Grid>
                    <Grid item sm={6}>
                        <Field name="time" component={renderTimePicker} label="وقت الانتهاء"/>
                    </Grid>

                    <Grid item xs={12}>
                        <Button type="submit" variant="contained" color="primary">Submit</Button>
                    </Grid>

                    </Grid>
                </form>
            </Dialog>
            </MuiThemeProvider>
        );
    }
}

const required = value => value ? undefined : 'Required';

export default reduxForm({
    form: 'GoalNewForm'
})(GoalNew);