import React from 'react';
import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem
} from 'reactstrap';
import { Link } from 'react-router-dom';
import LoginForm from '../login';
import TaskNew from '../task_new';

export default class Example extends React.Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            isOpen: false
        };
    }
    toggle() {
        this.setState({
            isOpen: !this.state.isOpen
        });
    }

    mobile = () => {
        localStorage.setItem('token', '560ada8c525156d6441fec29ea2910571eb1f35c');
    };

    render() {
        return (
            <div style={{direction: "rtl"}}>
                <Navbar color="light" light expand="md">
                    <NavbarBrand href="/">الرئيسية</NavbarBrand>
                    <NavbarToggler onClick={this.toggle} />
                    <Collapse isOpen={this.state.isOpen} navbar>
                        <Nav className="ml-auto" navbar>
                            <NavItem>
                                <Link to='/goal/goal'>
                                    <NavLink>الأهداف</NavLink>
                                </Link>
                            </NavItem>
                            <NavItem>
                                <Link to='/days'>
                                    <NavLink>Calendar</NavLink>
                                </Link>
                            </NavItem>
                            <NavItem>
                            <TaskNew>
                                <NavLink>Add Task</NavLink>
                            </TaskNew>
                            </NavItem>
                            <UncontrolledDropdown nav inNavbar>
                                <DropdownToggle nav caret>
                                    Options
                                </DropdownToggle>
                                <DropdownMenu right>
                                    <DropdownItem>
                                        Option 1
                                    </DropdownItem>
                                    <DropdownItem>
                                        Option 2
                                    </DropdownItem>
                                    <DropdownItem divider />
                                    <DropdownItem>
                                        Reset
                                    </DropdownItem>
                                </DropdownMenu>
                            </UncontrolledDropdown>
                            <NavItem>
                                <LoginForm />
                            </NavItem>
                            <NavItem>
                                <NavLink onClick={this.mobile}>mobile</NavLink>
                            </NavItem>
                        </Nav>
                    </Collapse>
                </Navbar>
            </div>
        );
    }
}