import _ from 'lodash';
import React from 'react';
import { 
    ListGroup,
    ListGroupItem,
    Badge
} from 'reactstrap';
import Link from 'react-router-dom/Link';
import { fetchGoals } from '../../actions/index';
import { connect } from 'react-redux';
import GoalNew from '../goal_new';
import Button from '@material-ui/core/Button';

const styles = {
    ListGroup: {
        textAlign: "right",
        direction: "rtl",
        margin: "20px 0"
    },
    button: {
        marginBottom: 30
    }
}

class List extends React.Component {


    componentDidMount() {
        this.props.fetchGoals();
    }


    renderList = () => {
        return _.map(this.props.goals, goal => {
            var time_h = parseInt(goal.total_time/60, 10);
            var time_m = goal.total_time - time_h * 60
            return (
                <Link to={`/goal/goal/${goal.id}`} key={goal.id}>
                    <ListGroupItem className="justify-content-between">
                        {goal.title}
                        <Badge className="float-left" pill>
                            {`${time_h}:${time_m}`}
                        </Badge>
                    </ListGroupItem>
                </Link>
            );
        });
    };

    render() {
        return (
            <div className="container">
                
                <ListGroup style={styles.ListGroup}>
                    {this.renderList()}
                </ListGroup>
                <GoalNew>
                    <Button variant="contained" color="primary">Add Goal</Button>
                </GoalNew>
            </div>
        );
    }
}


function mapStateToProps(state) {
    return { goals: state.goals };
}
export default connect(mapStateToProps, {fetchGoals})(List);