import React, { Component } from 'react';
import axios from 'axios';
import _ from 'lodash';
import TextField from '@material-ui/core/TextField'; 
import { href } from '../utils';
import Tasks from './tasks';


class Day extends Component {


    constructor(props){
        super(props);

        this.state = {
            day: null,
            report: {},
            newReport: ''
        }
    }

    componentDidMount() {
        axios({
            method: 'get',
            url: `${href}api/report/${this.props.match.params.id}/`,
            headers: {
                Authorization: `Token ${localStorage.getItem('token')}`
            }
        }).then((response) => {
            this.setState(() => {
                return {
                    report: response.data['report'],
                    day: response.data['day']
                };
            });
        });
    }

    renderDate = () => {
        if(this.state.day){
            return this.state.day.date;
        }
        return 'loading';
    }

    addReport = () => {
        axios({
            method: 'post',
            url: `${href}api/report/${this.props.match.params.id}/`,
            data: {
                content: this.state.newReport
            },
            headers: {
                Authorization: `Token ${localStorage.getItem('token')}`
            }
        }).then((response) => {
            this.setState({
                report: response.data
            });
        });

    };

    updateReport = () => {
        this.setState({
            newReport: this.state.report.content,
            report: {}
        });
    };

    renderReport = () => {
      if(_.isEmpty(this.state.report)){
          return (
              <div>
                  <TextField
                    style={{ margin: 8 }}
                    placeholder="اضف تقرير"
                    fullWidth
                    margin="normal"
                    variant="outlined"
                    multiline
                    rowMin="4"
                    InputLabelProps={{
                        shrink: true,
                    }}
                    value = {
                        this.state.newReport
                    }
                    onChange = {
                        (event) => this.setState({
                            newReport: event.target.value
                        })
                    }
                  />
                  <button className="btn btn-primary" onClick={this.addReport}>Add Report</button>
              </div>
          );
      }else{
          return (
              <div>
                  <button className="btn btn-primary" onClick={this.updateReport}>Update Report</button>
                  <div className="col-md-10">
                      <br />
                      <pre className="report">{this.state.report.content}</pre>
                      <br />
                  </div>
              </div>
          );
      }
    };

    render() {
        if(!this.state.day){
            return <h2>loading...</h2>;
        }
        const { id } = this.props.match.params;
        return (
            <div className="container" style={{textAlign: "right", direction: "rtl"}}>
                <Tasks day={id} date={this.state.day.date} />
                <br />
                <br />
                {this.renderReport()}
            </div>
        );
    }

}

export default Day;