import _ from 'lodash';

export default function (state = {}, action) {
    switch (action.type) {
        case "FETCH_GOALS":
            return _.mapKeys(action.payload.data, 'id');
        case "FETCH_GOAL":
            return {...state, [action.payload.data.id]: action.payload.data};
        default:
            return state;
    }
}