import React from 'react';
import axios from "axios";
import GoalTabs from './goal_tabs';
import { DateTime } from './date_time';
import Link from 'react-router-dom/Link';
import Paper from '@material-ui/core/Paper';
import { href } from '../utils';


class Subgoal extends React.Component {

    state = {
        goal: null
    };
    
    getGoal = () => {
        const { id } = this.props.match.params;
        axios({
            method: 'get',
            url: `${href}api/goal-detail/${id}/`,
            headers: {
                Authorization: `Token ${localStorage.getItem('token')}`
            }
        }).then(response => {
            this.setState({
                goal: response.data
            });
        });
    };

    componentDidMount() {
        this.getGoal();    
    }

    componentDidUpdate(prevProps) {
        if (this.props.match.params.id !== prevProps.match.params.id) {
            this.setState({ goal: null });
            this.getGoal();
        }
    }

    deleteGoal = () => {
        const { id } = this.props.match.params;
        axios.delete(`${href}api/goal/${id}/`, {
            headers: {
                Authorization: `Token ${localStorage.getItem('token')}`
            }
        }).then(() => {
            this.props.history.push("/goal/goal");
        });
    };
    

    render() {
        const { goal } = this.state;
        if (!goal) {
            return <div>Loading...</div>;
        }
        var jumbo_sytle = {
            marginTop: '10px',
            padding: 20,
            marginBottom: '0',
            width: "90%",
            marginLeft: "auto",
            marginRight: "auto",
            textAlign: "right",
            direction: "rtl"
        }
        const { id } = this.props.match.params;
        return (
            <div className="container">
                <Paper style={jumbo_sytle}>
                    <h1>{goal.title}</h1>
                    <br />
                    <pre>{goal.description}</pre>
                    <br />
                    <div className="row">
                        <div className="col-md-4">
                            الوقت الكلى
                        <span className="badge badge-primary badge-pill">{goal.total_time}h</span>
                        </div>
                        <br />
                        <div className="col-md-4">
                            الوقت المنقضى
                        <span className="badge badge-primary badge-pill">{goal.total_spend}h</span>
                        </div>
                        <br />
                        <div className="col-md-4">
                            تاريخ الانتهاء
                        <span className="badge badge-primary badge-pill">
                                <DateTime dateTime={goal.finished_at} />
                            </span>
                        </div>
                        <br />
                    </div>
                    <br />
                    <div className="row">
                        <div className="col-md-3">
                            <Link className="btn btn-warning" to={`/goal/update/${id}`}>Update</Link>
                        </div>
                        <div className="col-md-3">
                            <button className="btn btn-danger" onClick={this.deleteGoal}>Delete</button>
                        </div>
                        <div className="col-md-3">
                            <a className="btn btn-primary" href="#add-comment">Add comment</a>
                        </div>
                    </div>
                    <br />
                    <hr />
                    <GoalTabs goal_id={id} />
                </Paper>
                <br />
            </div>
        ); // end of return
    }


}



export default Subgoal;

