import React from 'react';
import _ from 'lodash';
import { 
    ListGroup, 
    ListGroupItem,
    Badge
} from 'reactstrap';
import Link from 'react-router-dom/Link';
import axios from 'axios';
import { href } from '../utils';
import GoalNew from './goal_new';
import Button from '@material-ui/core/Button';

const styles = {
    ListGroup: {
        textAlign: "right",
        direction: "rtl",
        margin: "20px 0"
    },
    button: {
        marginBottom: 30
    }
}

class Subgoals extends React.Component {

    state = {
        goals: null
    }

    componentDidMount() {
        this.fetchGoals();
    }

    fetchGoals = () => {
        const { id } = this.props;
        axios({
            method: 'get', 
            url: `${href}api/goal/${id}/subgoals/`,
            headers: {
                Authorization: `Token ${localStorage.getItem('token')}`
            }
        }).then(response => {
            this.setState({
                goals: _.mapKeys(response.data, 'id')
            });
        });
    };

    renderList = () => {
        return _.map(this.state.goals, goal => {
            var time_h = parseInt(goal.total_time / 60, 10);
            var time_m = goal.total_time - time_h * 60
            return (
                <Link to={`/goal/subgoal/${goal.id}`} key={goal.id}>
                    <ListGroupItem className="justify-content-between">
                        {goal.title}
                        <Badge className="float-left" pill>
                            {`${time_h}:${time_m}`}
                        </Badge>
                    </ListGroupItem>
                </Link>
            );
        });
    };

    render() {
        return (
            <div>
                <ListGroup style={styles.ListGroup}>
                    {this.renderList()}
                </ListGroup>
                <GoalNew goal_id={this.props.id} refresh={this.fetchGoals}>
                    <Button variant="contained" color="primary">Add Subgoal</Button>
                </GoalNew>
            </div>
        );
    }
}


export default Subgoals;