import React from 'react';
import axios from 'axios';
import _ from 'lodash';
import Link from 'react-router-dom/Link';
import { href } from '../utils';
import TaskNew from './task_new';
import Button from '@material-ui/core/Button';

class Tasks extends React.Component {

    state = {
        tasks: null
    };

    fetchTasks = () => {
        const params = (this.props.day)? {day: this.props.day}: { goal: this.props.goal};
        axios({
            method: 'get',
            url: `${href}api/task/`,
            params: params,
            headers: {
                Authorization: `Token ${localStorage.getItem('token')}`
            }
        }).then((response) => {
            this.setState(() => {
                return {
                    tasks: _.mapKeys(response.data, 'id')
                };
            });
        });
    };

    componentDidMount(){
        this.fetchTasks();
    }

    renderList = (type) => {
        return _.map(this.state.tasks, task => {
            if (task.task_type === type) {
                var duration_h = parseInt(task.duration/60, 10);
                var duration_m = task.duration - duration_h * 60
                return (
                    <Link to={`/task/${task.id}`} key={task.id}>
                        <li className="list-group-item d-flex justify-content-between align-items-center">
                            {task.title}
                            <span className={`badge ${type === 'a' ? 'badge-danger' : 'badge-primary'}`}>
                            {`${duration_h}:${duration_m}`}
                            </span>
                        </li>
                    </Link>
                );
            }else {
                return null;
            }
        });
    };


    render() {
        if(!this.state.tasks){
            return <h2>Loading...</h2>;
        }

        return (
            <div className="row">
                <div className="col-md-6">
                    <ul className="list-group list">
                        <h4>التخطيط</h4>
                        {this.renderList('a')}
                    </ul>
                    <TaskNew
                        type="a"
                        goal={this.props.goal}
                        day={this.props.day}
                        date={this.props.date}
                        refresh={this.fetchTasks}
                    >
                    <Button variant="contained" color="secondary"
                        style={{marginTop: 10}} 
                    >Add Task</Button>
                </TaskNew>
                </div>
                <div className="col-md-6">
                    <ul className="list-group list">
                        <h4>التنفيذ</h4>
                        {this.renderList('b')}
                    </ul>
                    <TaskNew
                        type="b"
                        goal={this.props.goal}
                        day={this.props.day}
                        date={this.props.date}
                        refresh={this.fetchTasks}
                    >
                        <Button variant="contained" color="primary"
                            style={{marginTop: 10}} 
                        >Add Task</Button>
                    </TaskNew>
                </div>
            </div>
        );
    }
}

export default Tasks;
