import React from 'react';
import {
    Field,
    reduxForm
} from 'redux-form';
import {
    renderField, 
    renderDatePicker, 
    renderTimePicker, 
    renderTextarea,
    renderSelectField
} from './form-component';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import moment from 'moment';
import axios from 'axios';
import { withStyles } from '@material-ui/core/styles';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { href } from '../utils';


const styles = theme => ({
    // paper: {
    //     position: 'fixed',
    //     top: '50%',
    //     left: '50%',
    //     transform: 'translate(-50%, -50%)',
    //     margin: 0,
    //     padding: theme.spacing.unit * 2,
    //     maxWidth: 650,
    //     overflow: 'auto'
    // }
});


class TaskNew extends React.Component {

    state = {
        open: false,
        goals: null
    };

    componentDidMount() {
        axios({
            method: 'get',
            url: `${href}api/get-user-goals/`,
            headers: {
                Authorization: `Token ${localStorage.getItem('token')}`
            }
        }).then(response => {
            this.setState({
                goals: response.data
            });
            if (this.props.goal) {
                this.props.change('goal', this.props.goal);
            }
            if (this.props.day) {
                const date = new Date(this.props.date);
                this.props.change('date', date);
            }
            if (this.props.type) {
                this.props.change('task_type', this.props.type);
            }
        });
    }

    onSubmit = (values) => {
        var date = moment(values.date).format("YYYY-MM-DD");
        var stime = moment(values.time).format("HH:mm:ss");
        var newValues = {...values, date, stime};
        console.log(values);
        axios({
            method: 'post',
            url: `${href}api/task/`, 
            data: newValues,
            headers: {
                Authorization: `Token ${localStorage.getItem('token')}`
            }
        }).then(() => {
            if(this.props.refresh){
                this.props.refresh();
            }
            this.handleClose();
        });
    }

    handleOpen = () => {
        this.setState({
            open: true
        });
    };

    handleClose = () => {
        this.setState({
            open: false
        });
    };
    
    renderGoals = () => {
        return this.state.goals.map(goal => {
            return <option value={goal.id}>{goal.title}</option>;
        });
    };

    render(){
        if(!this.state.goals){
            return <h2>Loading...</h2>;
        }
        const { handleSubmit } = this.props;
        return (
            <MuiThemeProvider>
                <div onClick={this.handleOpen}
                  style={{float: "left"}}  
                >{this.props.children}</div>
                <Dialog 
                    onClose={this.handleClose} 
                    aria-labelledby="add new task" 
                    open={this.state.open}
                    style={{direction:"rtl", textAlign: "right"}}
                    scroll="body"
                >
                    <DialogTitle>إضافة مهمة جديد</DialogTitle>
                    <form onSubmit={handleSubmit(this.onSubmit)} style={{padding:12}}>
                        <Grid container spacing={24}>
                        
                        <Grid item xs={12}>
                            <Field name="title" component={renderField} type="text" label="المهمة" validate={required} />
                        </Grid>
                        
                        <Grid item sm={6}>
                            <Field name="date" component={renderDatePicker} label="التاريخ "/>
                        </Grid>
                        <Grid item sm={6}>
                            <Field name="stime" component={renderTimePicker} label="الوقت"/>
                        </Grid>

                        <Grid item sm={6}>
                            <Field name="duration" component={renderField} type="number" label="المدة بالدقائق" validate={required} />
                        </Grid>
                        <Grid item sm={6}>
                            <Field name="task_type" component={renderField} type="text" label="task type" validate={required} />
                        </Grid>

                        <Grid item xs={12}>
                            <Field name="description" component={renderTextarea} label="التفاصيل" />
                        </Grid>
                        
                        <Grid item xs={12}>
                            <Field
                              name="goal"
                              component={renderSelectField}
                              label="تتبع هدف"
                            >
                                <option value="" />
                                {this.renderGoals()}
                            </Field>
                        </Grid>
                        <Grid item xs={12}>
                            <Button type="submit" variant="contained" color="primary">Submit</Button>
                        </Grid>

                        </Grid>
                    </form>
                </Dialog>
            </MuiThemeProvider>
        );
    }
}

const required = value => value ? undefined : 'Required';

export default reduxForm({
    form: 'TaskNewForm'
})(withStyles(styles)(TaskNew));